"use client";

import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { updateOrder } from "../api";
import { Order } from "../types";
import { orderSchema, OrderFormData } from "./validation";

interface Props {
  open: boolean;
  onClose: () => void;
  order: Order | null;
  refreshData: () => void;
}

export function EditOrderDialog({
  open,
  onClose,
  order,
  refreshData,
}: Props) {
  const [form, setForm] = useState<OrderFormData>({
    customerName: "",
    amount: 0,
    status: "OPEN",
  });

  const [errors, setErrors] = useState<
    Partial<Record<keyof OrderFormData, string>>
  >({});
  const [loading, setLoading] = useState(false);

  // ðŸ”¹ Populate form when order changes
  useEffect(() => {
    if (order) {
      setForm({
        customerName: order.customerName,
        amount: order.amount,
        status: order.status,
      });
      setErrors({});
    }
  }, [order]);

  if (!order) return null;

  const handleUpdate = async () => {
    setErrors({});

    // ðŸ”¹ Zod validation
    const result = orderSchema.safeParse(form);

    if (!result.success) {
      const fieldErrors: Partial<Record<keyof OrderFormData, string>> = {};
      result.error.issues.forEach((issue) => {
        fieldErrors[issue.path[0] as keyof OrderFormData] = issue.message;
      });
      setErrors(fieldErrors);
      return;
    }

    try {
      setLoading(true);

      await updateOrder(order.id, result.data);

      toast.success(`Order ${order.orderNo} updated`);
      onClose();
      refreshData();
    } catch {
      toast.error("Failed to update order");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Edit Order</DialogTitle>
        </DialogHeader>

        <div className="space-y-3 mt-2">
          {/* Order No (readonly) */}
          <Input value={order.orderNo} readOnly />

          {/* Customer Name */}
          <Input
            placeholder="Customer Name"
            value={form.customerName}
            onChange={(e) =>
              setForm({ ...form, customerName: e.target.value })
            }
          />
          {errors.customerName && (
            <p className="text-sm text-red-500">{errors.customerName}</p>
          )}

          {/* Amount */}
          <Input
            type="number"
            placeholder="Amount"
            value={form.amount || ""}
            onChange={(e) =>
              setForm({ ...form, amount: Number(e.target.value) })
            }
          />
          {errors.amount && (
            <p className="text-sm text-red-500">{errors.amount}</p>
          )}

          {/* Status */}
          <select
            className="border rounded-md p-2"
            value={form.status}
            onChange={(e) =>
              setForm({
                ...form,
                status: e.target.value as OrderFormData["status"],
              })
            }
          >
            <option value="OPEN">OPEN</option>
            <option value="PENDING">PENDING</option>
            <option value="INPROGRESS">INPROGRESS</option>
            <option value="COMPLETED">COMPLETED</option>
            <option value="CANCELLED">CANCELLED</option>
          </select>
        </div>

        <Button
          className="w-full mt-4"
          onClick={handleUpdate}
          disabled={loading}
        >
          {loading ? "Updating..." : "Update"}
        </Button>
      </DialogContent>
    </Dialog>
  );
}





