"use client";

import * as React from "react";
import {
  useReactTable,
  getCoreRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  getFilteredRowModel,
  flexRender,
  ColumnDef,
  SortingState,
  FilterFn,
} from "@tanstack/react-table";
import { Order } from "../types";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { toast } from "sonner";

interface OrdersTableProps {
  data: Order[];
  page: number;
  pageSize: number;
  total: number;
  loading: boolean;
  onPageChange: (page: number) => void;
  refreshData: () => void;
  columns: ColumnDef<Order>[];
}

export const OrdersTable: React.FC<OrdersTableProps> = ({
  data,
  page,
  pageSize,
  total,
  loading,
  onPageChange,
  refreshData,
  columns,
}) => {
  const [globalFilter, setGlobalFilter] = React.useState("");
  const [sorting, setSorting] = React.useState<SortingState>([]);
  const [columnFilters, setColumnFilters] = React.useState<any[]>([]); // column filters state

  const table = useReactTable({
    data,
    columns,
    state: {
      sorting,
      globalFilter,
      columnFilters,
      pagination: { pageIndex: page - 1, pageSize },
    },
    getCoreRowModel: getCoreRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    manualPagination: true,
    manualSorting: true,
    manualFiltering: true,
    pageCount: Math.ceil(total / pageSize),
    onSortingChange: setSorting,
    onColumnFiltersChange: setColumnFilters,
  });

  const clearFilters = () => {
    setGlobalFilter("");
    setColumnFilters([]);
  };

  return (
    <div className="rounded-md border p-2 space-y-2 relative">
      {/* Global search + clear filters */}
      <div className="flex flex-wrap items-center justify-between gap-2 mb-2">
        <Input
          placeholder="Search orders..."
          value={globalFilter}
          onChange={(e) => setGlobalFilter(e.target.value)}
          className="max-w-sm"
        />
        <Button size="sm" variant="outline" onClick={clearFilters}>
          Clear Filters
        </Button>
      </div>

      {/* Column filters */}
      <div className="flex flex-wrap gap-2 mb-2">
        {/* Example: Customer filter */}
        <Input
          placeholder="Filter by customer"
          value={
            (columnFilters.find((f) => f.id === "customerName")?.value as string) || ""
          }
          onChange={(e) => {
            const value = e.target.value;
            setColumnFilters((prev) => [
              ...prev.filter((f) => f.id !== "customerName"),
              ...(value ? [{ id: "customerName", value }] : []),
            ]);
          }}
          className="max-w-xs"
        />

        {/* Example: Status multi-select */}
        <div className="flex gap-2 items-center">
          {["OPEN", "PENDING", "INPROGRESS", "COMPLETED", "CANCELLED"].map((status) => {
            const selectedStatuses =
              (columnFilters.find((f) => f.id === "status")?.value as string[]) || [];
            return (
              <label key={status} className="flex items-center gap-1">
                <Checkbox
                  checked={selectedStatuses.includes(status)}
                  onCheckedChange={(checked) => {
                    let newStatuses = selectedStatuses.filter((s) => s !== status);
                    if (checked) newStatuses.push(status);
                    setColumnFilters((prev) => [
                      ...prev.filter((f) => f.id !== "status"),
                      ...(newStatuses.length > 0 ? [{ id: "status", value: newStatuses }] : []),
                    ]);
                  }}
                />
                <span className="text-sm">{status}</span>
              </label>
            );
          })}
        </div>
      </div>

      {/* Table */}
      <div className="relative">
        {loading && (
          <div className="absolute inset-0 z-10 flex items-center justify-center bg-white/70">
            <div className="w-12 h-12 border-4 border-blue-500 border-dashed rounded-full animate-spin"></div>
          </div>
        )}
        <Table>
          <TableHeader>
            {table.getHeaderGroups().map((headerGroup) => (
              <TableRow key={headerGroup.id}>
                {headerGroup.headers.map((header) => (
                  <TableHead
                    key={header.id}
                    className="cursor-pointer select-none"
                    onClick={header.column.getToggleSortingHandler()}
                  >
                    {flexRender(header.column.columnDef.header, header.getContext())}
                    {{
                      asc: " ðŸ”¼",
                      desc: " ðŸ”½",
                    }[header.column.getIsSorted() as string] ?? null}
                  </TableHead>
                ))}
              </TableRow>
            ))}
          </TableHeader>
          <TableBody>
            {table.getRowModel().rows.length > 0 ? (
              table.getRowModel().rows.map((row) => (
                <TableRow key={row.id}>
                  {row.getVisibleCells().map((cell) => (
                    <TableCell key={cell.id}>
                      {flexRender(cell.column.columnDef.cell, cell.getContext())}
                    </TableCell>
                  ))}
                </TableRow>
              ))
            ) : (
              <TableRow>
                <TableCell colSpan={columns.length}>
                  {loading ? "Loading..." : "No orders found"}
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </div>

      {/* Pagination */}
      <div className="flex justify-between items-center mt-4">
        <Button size="sm" disabled={page <= 1} onClick={() => onPageChange(page - 1)}>
          Previous
        </Button>
        <span>
          Page {page} of {Math.ceil(total / pageSize)}
        </span>
        <Button
          size="sm"
          disabled={page >= Math.ceil(total / pageSize)}
          onClick={() => onPageChange(page + 1)}
        >
          Next
        </Button>
      </div>

      {/* Active filters display */}
      {columnFilters.length > 0 && (
        <div className="flex flex-wrap gap-2 mt-2 text-sm text-muted-foreground">
          Active Filters:
          {columnFilters.map((f) => (
            <span key={f.id} className="bg-gray-100 px-2 py-1 rounded">
              {f.id}: {Array.isArray(f.value) ? f.value.join(", ") : f.value}
            </span>
          ))}
        </div>
      )}
    </div>
  );
};




