"use client";

import React, { useEffect, useState } from "react";
import { OrdersTable } from "./orders/components/orders-table";
import { getOrders } from "./orders/api";
import { Order } from "./orders/types";
import { toast } from "sonner";
import { getColumns } from "./orders/columns";
import { AddOrderDialog } from "./orders/components/add-order-dialog";
import { Button } from "@/components/ui/button";

export default function OrdersPage() {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(false);

  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10); // dynamic
  const [total, setTotal] = useState(0);

  const [addDialogOpen, setAddDialogOpen] = useState(false);

  const fetchOrders = async (pageNumber = page, size = pageSize) => {
    setLoading(true);
    try {
      const res = await getOrders(pageNumber, size);
      setOrders(res.data.items);
      setTotal(res.data.total);
      setPage(res.data.page);
    } catch {
      toast.error("Failed to fetch orders");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOrders(page, pageSize);
  }, [page, pageSize]);

  return (
    <div className="p-4 space-y-4 relative">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Orders</h1>

        {/* Add Order Button */}
        <Button onClick={() => setAddDialogOpen(true)}>Add Order</Button>
      </div>

      {/* Rows per page selector */}
      <div className="flex items-center gap-2">
        <span className="text-sm text-muted-foreground">Rows per page</span>

        <select
          className="border rounded-md px-2 py-1 text-sm"
          value={pageSize}
          onChange={(e) => {
            setPage(1);
            setPageSize(Number(e.target.value));
          }}
        >
          {[10, 20, 50, 100].map((size) => (
            <option key={size} value={size}>
              {size}
            </option>
          ))}
        </select>

        <span className="text-sm text-muted-foreground">Total: {total}</span>
      </div>

      {/* Add Order Dialog */}
      <AddOrderDialog
        open={addDialogOpen}
        onClose={() => setAddDialogOpen(false)}
        refreshData={() => fetchOrders(1, pageSize)}
      />

      {/* Table + Loading Overlay */}
      <div className="relative">
        {loading && (
          <div className="absolute inset-0 z-10 flex items-center justify-center bg-white/70">
            <div className="w-12 h-12 border-4 border-blue-500 border-dashed rounded-full animate-spin"></div>
          </div>
        )}

        <OrdersTable
          data={orders}
          page={page}
          pageSize={pageSize}
          total={total}
          loading={loading}
          onPageChange={setPage}
          refreshData={fetchOrders}
          columns={getColumns(fetchOrders)}
        />
      </div>
    </div>
  );
}






